.brand__card {
  background: $white;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.17);
  border-radius: 4px;
  margin-bottom: 16px;
  height: 213px;
  position: relative;
  @include center;
  flex-direction: column;
  text-decoration: none;
  width: calc(100% - 9px);
  padding: 0 7px;
  @include tr;
  &:hover {
    box-shadow: 0 10px 20px rgba(0,0,0,.2); }
  @include r($mobile) {
    height: 155px;
    margin-bottom: 4px;
    width: 100%; }
  &__image {
    img {
      height: 70px; } }
  h4 {
    font-size: 14px;
    letter-spacing: 1px;
    text-align: center;
    color: #0e0e0e;
    position: absolute;
    bottom: 15px;
    @include r($mobile) {
      bottom: 10px; } }
  span {
    opacity: 0;
    visibility: hidden;
    text-decoration: none;
    color: $white;
    border: 1px solid $white;
    border-radius: 3px;
    padding: 8px 9px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 13px;
    text-transform: uppercase;
    white-space: nowrap;
    font-weight: 600;
    @include tr;
    @include r($mobile) {
      font-size: 10px; } }
  &:hover {
    background: $primary;
    h4 {
      color: $white; }
    span {
      opacity: 1;
      visibility: visible; }
    img {
      opacity: 0;
      visibility: hidden; } }
  &.category {
    h4 {
      font-weight: normal;
      text-transform: uppercase; } } }
.category__block {
  &:nth-of-type(1) .brand__card__image img {
    height: 46px; }
  &:nth-of-type(2) .brand__card__image img {
    height: 70px; }
  &:nth-of-type(3) .brand__card__image img {
    height: 62px; }
  &:nth-of-type(4) .brand__card__image img {
    height: 70px; }
  &:nth-of-type(5) .brand__card__image img {
    height: 62px; } }
